.dacris__catalogue {
    width: 100%;
    height: fit-content;
}

.dacris__catalogue-content {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    row-gap: 25px;
    margin-top: 70px;
    background: #FFFFFF;
}

.dacris__catalogue-content-title {
    color: #000000;
    font-size: 125px;
    font-family: 'Playlist Script';
}

.dacris__catalogue-content-buttons {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 50px;
}

.dacris__catalogue-content-buttons-button {
    width: 550px;
    height: 550px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    border-radius: 50px;
    background: url(../../assets/image2.jpg);
    background-size: cover;
    background-position: center;
    text-decoration: none;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, .2);
    transition: .5s ease-in-out all;
}

.dacris__catalogue-content-buttons-button:hover {
    transform: scale(1.1);
}

.dacris__catalogue-content-buttons-button div {
    width: 90%;
    height: 90%;
    border: 10px solid #FFFFFF;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dacris__catalogue-content-buttons-button h1 {
    font-family: var(--font-family-title);
    color: #FFFFFF;
    font-size: 900%;
}