.round_textfield input{
    background-color: white;
    outline: none;
    width: 20rem;
    height: 2rem;
    border: none;
    padding-left: 40px;
    border-radius: 20px;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.2);
    font-size: 1.3rem;
    font-family: 'Montserrat';
}

.round_textfield input:focus {
    box-shadow: 0px 0px 6px rgba(6, 65, 153, 0.418);
}

.round_textfield svg{
    position: relative;
    top: -4px;
    left: 35px;
    font-size: 1.5rem;
}

.form_textfield {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
}

.form_textfield h1, .form_dropdown h1 {
    font-family: 'League Gothic';
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    text-shadow: none;
    letter-spacing: 0.03em;
}

.form_textfield input, .form_dropdown select{
    background-color: white;
    outline: darkgray solid 1px;
    width: 20rem;
    height: 2rem;
    border: none;
    padding-left: 5px;
    border-radius: 5px;
    font-size: 20px;
    font-family: 'Montserrat';
    resize: none;
}

.form_textfield p, .form_dropdown p {
    margin: 0;
    color: red;
    font-size: 12px;
}

.form_textfield textarea{
    background-color: white;
    outline: darkgray solid 1px;
    width: 20rem;
    border: none;
    padding-left: 5px;
    border-radius: 5px;
    font-size: 20px;
    font-family: 'Montserrat';
    resize: none;
}

@media screen and (max-width: 1536px) {
    .round_textfield input {
        width: 16rem;
        height: 1.5rem;
        font-size: 1rem;
        padding-left: 30px;
    }
    .round_textfield svg {
        position: relative;
        top: 0px;
        left: 25px;
        font-size: 1.2rem;
    }

    .form_textfield input, .form_dropdown select {
        width: 16rem;
        height: 1.5rem;
        font-size: 1rem;
    }
    .form_textfield textarea {
        width: 16rem;
    }
    .form_textfield h1, .form_dropdown h1 {
        font-size: 1.2rem;
    }
}

@media (min-width: 1537px) and (max-width: 1600px) {
    .round_textfield input {
        width: 16rem;
        height: 1.8rem;
        font-size: 1.1rem;
        padding-left: 30px;
    }
    .round_textfield svg {
        position: relative;
        top: -2px;
        left: 25px;
        font-size: 1.2rem;
    }
}